import { HttpClient, HttpParams} from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

import swal from 'sweetalert';

@Injectable()
export class RestProvider {

  apiUrl='http://www.technofarm.com.br/webservices/tfwebservice.php'

  capiUrl='http://www.technofarm.com.br/api/v1/';


  reqOpts:any;


  constructor(public http: HttpClient) {
    console.log('constructor Rest Provider');
  }

    getRest(myJson:any) {


      this.reqOpts = {
        headers: {
          'AuthType':'Basic',
          'Content-Type':'application/json',
          'Accept':'application/json',
          'Authorization':'Basic YWRtaW46cGFzc3dvcmQ=',
          'Access-Control-Allow-Origin':'*',
          'Access-Control-Allow-Methods': 'GET, POST, DELETE, PUT, OPTIONS',
          'Access-Control-Allow-Headers': 'origin, content-type, accept, authorization, x-request-with',
          'Access-Control-Allow-Credentials':'true'
        },
        params: new HttpParams()
      };
  
      console.log('url:'+this.apiUrl);
      console.log(myJson);
  
      return new Promise(resolve => {
        this.http.post(this.apiUrl,myJson, this.reqOpts)
          .subscribe(data => {
          resolve(data);}, 
        err => {
        console.log(err);
         
        });
        });
  
     
      }

  execGET(operation:any){


     this.reqOpts = {
      headers: {
        
        'Content-Type':'application/json',
        'token': '4xcUZzijQWi314zTdOUMN4QYMZ0Y73Y2zNNDTmV',
        'Accept':'application/json'},
      params: new HttpParams()
    };

    let completecapi=this.capiUrl+operation;

    console.log('url:'+completecapi);
    

    return new Promise(resolve => {
      this.http.get(completecapi,this.reqOpts)
        .subscribe(data => {
        resolve(data);}, 
      err => {
      console.log(err);
      swal("", "Para fazer o seu login, deve usar as mesmas credenciais (usuario e senha) que cadastrou no nosso site http://www.technofarm.com.br\nSe não se recorda do usuário e/ou senha, no site tem a possibilidade de solucionar esse tema.\nObrigado.", "error");
        
      });
      });


  }

}


