import { Component } from '@angular/core';
import { IonicPage, NavController,  ToastController,
  LoadingController } from 'ionic-angular';
  
//import { SignupPage } from '../signup/signup';
//import { WelcomePage} from '../welcome/welcome'
import { UnidprodPage} from '../unidprod/unidprod'
import { Storage } from '@ionic/storage'
import { /*Validators,*/ FormBuilder } from '@angular/forms';
//import { Md5 } from "md5-typescript";
import { RestProvider } from '../../providers/rest/rest';
import { SingletonService } from '../../providers/util/singleton.service';
//import { ListanoticiasPage }  from '../noticia/listanoticias/listanoticias';
import swal from 'sweetalert';



@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})




export class LoginPage {

  public data_1:any;
  public data_2:any;
  
  public loginform : any;
 
  erroremail=false;
  errorsenha = false;
  errorcpf = false;
  errornome=false;
  errorano = false;
  errorsexo=false;



  msgemail = "";
  msgsenha = "";

  public struid:any;
  public strpwd:any;

  public FirstStageUser:any;
  public SecondStageUser:any;

  public struser:any;
  public strmd5notice:any;
  public strmysenha:any;
  
  public curruser: any = [{
    uemail: "",
    unome: "",
    ucpf:"",
    umd5pwd:""
  }]

  usuario: any;

  constructor(     
  
    public form: FormBuilder, public navCtrl: NavController, 
    public loadingCtrl: LoadingController,
    public toastCtrl:ToastController,
    public storage: Storage,
    public singleton: SingletonService,
    public restProvider:RestProvider) {

      this.loginform = form.group({
              uid: "",
              pwd: ""});
           
            



        this.dopageoperations();
        

  }


dopageoperations(){
    
  }


presenttoast() {
      let toast = this.toastCtrl.create({
        message: this.strmd5notice,
        position: 'bottom',
        showCloseButton: true,
     
        closeButtonText:'Fechar'
      });
    
        toast.onDidDismiss(() => {
        
      });
    
      toast.present();
    }


dorefresh(refresher) {
    console.log('Begin async operation', refresher);
    setTimeout(() => {
      console.log('Async operation has ended');
      refresher.complete();
    }, 2000);
  }

deletelocalaccount(){
    this.storage.remove('senacstore');
    this.navCtrl.setRoot(this.navCtrl.getActive().component);
      
  }


alerttoast(msg:string) {
    console.log('Toast INIT');
    let toast = this.toastCtrl.create({
      message: msg,
      position: 'middle',
      duration: 3000
    });
  
      toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });
  }

validatelogin()
    {
      let operation1="user/login/"+this.struid+"/"+this.strpwd;
      console.log(operation1);

      this.restProvider.execGET(operation1)
        .then(data1 => {
           this.data_1=data1;
    

           
           if (this.data_1.msg == "Authenticated"){
              let operation2="user/detail/username/"+this.struid;
  
              this.restProvider.execGET(operation2)
                .then(data2 => {
                this.data_2=data2;
                this.singleton.loginuid=this.data_2.id;
                this.singleton.loginname=this.data_2.username;
                this.singleton.loginstate=true;
                console.log("singleton_uid1:"+this.singleton.loginuid);
                this.alerttoast("Aguarde, por favor... Sincronizando seus dados.");
                this.navCtrl.setRoot('UnidprodPage',{single:this.singleton});

              });
            }

            
            
      });


        
       
      
      }
  
   

    



  }





