import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams , Content} from 'ionic-angular';
//import { RoomPage } from '../room/room';
import * as firebase from 'firebase';
//import { FirebaseProvider } from '../../../providers/firebase/firebase'
import { SingletonService } from '../../../providers/util/singleton.service';
import { ListanoticiasPage } from '../../noticia/listanoticias/listanoticias';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  [x: string]: any;

  @ViewChild(Content) content: Content;

  data = { type: '', nickname: '', message: '' };

  chats = [];
  roomkey: string;
  nickname: string;
  email:string;
  offStatus: boolean = false;
  message: string = '';
  admin:any=1;


  constructor(public singleton: SingletonService, public navCtrl: NavController, public navParams: NavParams) {

   
   this.roomkey = this.navParams.get("key") as string;
   this.nickname = this.singleton.loginname as string;
   this.email=this.singleton.loginemail;
   this.type = 'message';


  this.entrarnasala(this.roomkey, this.type, this.singleton. loginname, '',this.singleton.loginemail)


    this.message = '';
    this.data.message = '';
 

  firebase.database().ref('chatrooms/' + this.roomkey + '/chats').on('value', resp => {
      this.chats = [];
      this.chats = snapshotToArray(resp);
      setTimeout(() => {
        if (this.offStatus === false) {
          this.content.scrollToBottom(300);
        }
      }, 100);
    });
  }

  sendMessage() {
    this.enviarmensagem(
      this.roomkey,
      this.type,
      this.singleton.loginname,
      this.data.message,
      this.singleton.loginemail
      );

    this.data.message = '';
  }

exitChat() {
  
    this.sairdasala(this.roomkey,
      this.data.type,
      this.nickname,
      this.data.message,
      this.singleton.loginemail);

     this.offStatus = true;

    this.navCtrl.setRoot(ListanoticiasPage, {
      nickname: this.singleton.loginname
    });
  }


enviarmensagem(myroomkey, mytype, mynickname, mymessage, myemail) {
    firebase.database().ref('chatrooms/' + myroomkey + '/chats').push({
      type: mytype,
      user: this.singleton.loginname,
      message: mymessage,
      email: this.singleton.loginemail,
      sendDate: Date(),
      admin:"0"
    });
  }

sairdasala(myroomkey, mytype, mynickname, mymessage,myemail) {
    firebase.database().ref('chatrooms/' + myroomkey + '/chats').push({
      type: 'exit',
      user: this.singleton.loginname,
      message: this.singleton.loginname + ' saiu da sala.',
      email: this.singleton.loginemail,
      sendDate: Date(),
      admin:"0"
      });
  }

entrarnasala(myroomkey, mytype, mynickname, mymessage, myemail, ) {
  console.log('entrar na sala')  
  console.log(this.singleton)

    firebase.database().ref('chatrooms/' + myroomkey + '/chats').push({
      type: 'join',
      user: this.singleton.loginname,
      message: this.singleton.loginname + ' entrou na sala.',
      email: this.singleton.loginemail,
      sendDate: Date(),
      admin:"0"
    });

}

public mymessages(email) {
  return email == this.singleton.loginemail;


}

}

export const snapshotToArray = snapshot => {
  let returnArr = [];
  snapshot.forEach(childSnapshot => {
    let item = childSnapshot.val();
    item.key = childSnapshot.key;
    returnArr.push(item);
  });

  console.log(returnArr)
  return returnArr;
  //.filter(this.mymessages);
};


