import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RestProvider } from '../../../providers/rest/rest';
import 'rxjs/add/operator/map'
import { LoadingService } from "../../../providers/util/loading.service"

@IonicPage()
@Component({
  selector: 'page-listanoticias',
  templateUrl: 'listanoticias.html',
})
export class ListanoticiasPage {
    
   noticias: any;
   searchTerm: string = '';
    items: any;

    
  constructor(public navCtrl: NavController, public loader: LoadingService, public navParams: NavParams, public restProvider: RestProvider) {
    
    this.loader.presentWithMessage("Por favor, aguarde");

    
    this.loader.dismiss().then(() =>{
        this.getNoticias();
    });
  }  

  getNoticias() {
    this.restProvider.getRest('{"operacao":"getnoticias"}')
    .then(data => {
        console.log(data);
       
        this.noticias = data;
        });
    
    
  }
  goToNoticia(i){      
      console.log(i);
      console.log(this.noticias[i].AD0031TituloNoticia);
      this.navCtrl.push('SinglenoticiaPage', {noticia:this.noticias[i]})

    }
    filterItems(searchTerm){

        return this.noticias.filter((item) => {
            return item.AD0031TituloNoticia.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
        });
    } 
    setFilteredItems() {

        this.noticias = this.filterItems(this.searchTerm);

    }
    goToPesquisa(){      
      this.navCtrl.push('PesquisacursoPage')

    }

}



